import React, { Component } from 'react';
import './App.scss';
import Header from './Header'
import AppContent from './AppContent'

function NavItem(props){
  var className = props.active === true ? "active" : "";
  var icon = props.icon;
  return (
    <li className={className} onClick={props.onClickFunction}>
      <i className="material-icons">{icon}</i>
      {props.name}
    </li>
  );
}

class App extends Component {
  constructor(props){
    super(props);

    // Don't call this.setState() here!
    this.state = { 
      activeContent: "Home", 
      navItems : [
        {
          id: "tab_home",
          name: "Home",
          icon: "event"
        },
        {
          id: "tab_schedule",
          name: "Schedule",
          icon: "access_time"
        },
        {
          id: "tab_speakers",
          name: "Speakers",
          icon: "supervisor_account"
        },
        {
          id: "tab_venue",
          name: "Venue",
          icon: "location_on"
        }
      ]
    };

    // This binding is necessary to make `this` work in the callback
    this.changeContent = this.changeContent.bind(this);
  }

  changeContent(e, name){
    console.log(name);
    this.setState(state => ({
      activeContent: name
    }));
  }

  render() {
    var fullHeader = true;
    if(this.state.activeContent !== "Home"){
      fullHeader = false;
    }
    return (
      <div className="App">
        <Header fullHeader={fullHeader} />
        <nav className="navbar">
          <div className="container">
            <ul>
              {this.state.navItems.map(item => (
                <NavItem 
                  key={item.id}
                  className={item.id}
                  icon={item.icon}
                  name={item.name}
                  onClickFunction={(e) => this.changeContent(e, item.name)}
                  active={this.state.activeContent === item.name ? true : false}
                />
              ))}
            </ul>
          </div>
        </nav>
        <AppContent content={this.state.activeContent} />
      </div>
    );
  }
}

export default App;
