import React, { Component } from 'react';
import Schedule from './Schedule';
import Speakers from './Speakers';
import Venue from './Venue';

class AppContent extends Component {
	constructor(props){
		super(props);
    this.state = { activeContent: this.props.content };
	}

	render(){
		var activeContent = this.props.content;

		const Foo = (function() {
                if(activeContent === "Home"){
										return (
											<div className="home">
												<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec quis lacus et risus placerat consectetur eu sit amet mauris. Etiam imperdiet nulla massa, eu ornare quam ultricies vitae. Vestibulum in fermentum lectus, a porttitor nulla. Curabitur pellentesque feugiat urna sed mattis. Nullam vitae dapibus libero. Cras tincidunt lacus ante, eu vehicula elit iaculis nec. Praesent efficitur lacus a mauris auctor lacinia accumsan quis risus. Vestibulum ut fermentum mi. Curabitur aliquam, ex lobortis dignissim laoreet, dui dolor luctus magna, viverra faucibus leo enim eget justo. Sed gravida justo a ultrices vulputate. In porta nisi eu ipsum ultrices, at finibus odio vehicula. Vivamus a massa ante.</p>
											</div>
										)
									} else if(activeContent === "Schedule"){
										return <Schedule />
									} else if(activeContent === "Speakers"){
										return <Speakers />
									} else {
										return <Venue />
									}
            });

    var content = (<Foo />)

		return (
			<div className="appContent">
				<div className="container">
				{content}
				</div>
			</div>
		)
	}
}

export default AppContent;