import React, { Component } from 'react';
import './Venue.scss';
import IconPhone from './img/icons/phone_grey.png';

class Venue extends Component {
	render(){
		const map = navigator.onLine ? "" : <p className="offline-warning">*Access to the internet is required to load the map properly.</p>;
		return (
			<div className="venue">
				<h2>Venue</h2>
				<h3>Santiago Marriot Hotel</h3>
				<p className="venue-phone"><img src={IconPhone} alt="phone" />(56 2) 234 5656</p>
				<div className="venue-map" style={{backgroundColor:"#ccc", width:"100%", height:"200px"}}>
					<iframe src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d13323.576667407844!2d-70.5744547!3d-33.3999252!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x96175c40458868eb!2sSantiago+Marriott+Hotel!5e0!3m2!1sen!2scl!4v1553373683280" width="100%" height="200" frameBorder="0" style={{border:"0"}} allowFullScreen></iframe>
				</div>
				{map}
				<p className="venue-address">Av Presidente Kennedy 5741, Las Condes, Región Metropolitana</p>
			</div>
		)
	}
}

export default Venue;