import React, { Component } from 'react';
import HeaderBg from './img/header.png';

class Header extends Component {
	// constructor(props){
	// 	super(props);
	// }

	render(){
		var fullHeader = <div className="container">
          <h1>CharlieConf 2019</h1>
          <p className="date">April 16, 2019</p>
        </div>
		var minHeader = <div className="container">
          <h1>CharlieConf 2019</h1>
        </div>

    var header = this.props.fullHeader === true ? fullHeader : minHeader;

    var className =  this.props.fullHeader === true ? "header fullHeader" : "header minHeader";

		return(
      <header className={className} style={{backgroundImage: "url(" + HeaderBg + ")"}}>
        {header}
      </header>
		)
	}
}

export default Header;