import React, { Component } from 'react';

class SchedItem extends Component {
	render(){
		var mySpeaker = this.props.speaker;
		const Speaker = (function() {
											if(mySpeaker){
												return (<span className="ev-speaker">{mySpeaker}</span>)
											} else {
												return "";
											}
										})
		
		return (
			<div className={"event ev_" + this.props.block + " color_"+ this.props.color}>
				<span className="ev-title">{this.props.title}</span>
				<Speaker/>
				<span className="ev-place">{this.props.place}</span>
			</div>
		)
	}
}

export default SchedItem;