import React, { Component } from 'react';
import axios from 'axios';
import './Speakers.scss';
import Preloader from './img/loader.gif';
import IconTwitter from './img/icons/twitter.png';
import IconGithub from './img/icons/github.png';

function SpeakerItem(props){
	return (
		<div className="speaker">
			<div className="speaker-img">
				<img src={process.env.PUBLIC_URL + "/img/speakers/" + props.img} alt={props.name} />
			</div>
			<div className="speaker-name">
				<h3>{props.name}</h3>
			</div>
			<ul className="speaker-social">
				<li className="twitter"><a href={props.twitter}>
					<img src={IconTwitter} alt="Twitter"/> Twitter
				</a></li>
				<li className="github"><a href={props.github}>
					<img src={IconGithub} alt="Github"/> Github
				</a></li>
			</ul>
		</div>
	)
}

class Speakers extends Component {
	constructor(props){
		super(props);
		this.state = {
			loading: true,
			speakers : []
		}
	}


	componentDidMount() {
		axios.get('json/speakers.json')
			.then((data) => {
	      setTimeout(()=>{
		      this.setState({
		        loading: false,
		        speakers: data.data
		      })
		    }, 1000)
		   })
      .catch((error) => { console.log("error")});
      

	}


	render(){

		var speakersArray = this.state.speakers;

		return(
			<div className="speakers">
				<h2>Speakers</h2>
				<div className="speakers-container">
				
					{this.state.loading ? <img src={Preloader} alt="loading" className="preloader" /> :

						speakersArray.map(sp => (
							<SpeakerItem 
								key={sp.name}
								name={sp.name}
								img={sp.img}
								twitter={sp.twitter}
								github={sp.github}
							/>
						))
					}
				</div>
			</div>
		)
	}
}

export default Speakers;