import React, { Component } from 'react';
import axios from 'axios';
import SchedItem from './SchedItem';
import './Schedule.scss';
import Preloader from './img/loader.gif';

class Schedule extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	loading: true,
      schedule: []
    };
  }

	componentDidMount() {
		axios.get('json/schedule.json').then(data => {
      setTimeout(()=>{
      this.setState({
        loading: false,
        schedule: data.data
      })}, 1000)
      
    });
	}

	render(){
		return (
			<div className="schedule">
				<h2>Schedule</h2>
				<p className="sched-date">16 April, 2019</p>
				
				{this.state.loading ? <img src={Preloader} alt="loading" className="preloader" /> :

					<div className="sched-list">
							{this.state.schedule.map(b => (
								<div key={b.block} className="sched-block">
									<h3>{b.time}</h3>
									<div className="sched-container">
										{b.content.map((c, index) => (
											<SchedItem
												key={index}
												block={b.block}
												title={c.title}
												speaker={c.speaker}
												place={c.place}
												color={c.color}
											 />
										))}
									</div>
								</div>
							))}
					</div>

				}


			</div>
		)
	}
}

export default Schedule;